@import "../../global.scss";

.work {
    background-color: crimson;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow {
        height: 50px;
        position: absolute;
        cursor: pointer;

        // // media Query
        // @include mobile {
        //     display: none;
        // }

        &.left {
            left: 100px;
            transform: rotateY(180deg);
        }

        &.right {
            right: 100px;
        }
    }

    .slider {
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        padding-top: 30px;

        // // media Query
        // @include mobile {
        //     height: 100vh;
        //     flex-direction: column;
        //     justify-content: center;
        // }

        .container {
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item {
                width: 700px;
                height: 100%;
                background-color: #fff;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                // // media Query
                // @include mobile {
                //     width: 90%;
                //     height: 180px;
                //     margin: 5px 0;
                // }

                .left {
                    flex: 4;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer {
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        .imgContainer {
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            background-color: rgb(245, 179, 155);
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            img {
                                width: 25px;
                            }
                        }
                        h2 {
                            font-size: 20px;
                            // // media Query
                            // @include mobile {
                            //     font-size: 10px;

                            // }
                        }
                        p {
                            font-size: 13px;
                            // media Query
                            // @include mobile {
                            //     font-size: 9px;
                            //     // font-weight: 300;
                            // }
                        }
                        span {
                            font-size: 12px;
                            font-weight: 600;
                            text-decoration: underline;
                            cursor: pointer;
                        }
                    }
                }

                .right {
                    flex: 8;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;

                    img {
                        width: 800px;
                        height: 400px;
                        transform: rotate(-10deg);

                        // // media query
                        // @include mobile{
                        //     width: 400px;
                        //     height: 200px;
                        // }
                    }
                }
            }
        }
    }
}
